import React, { useState, useEffect } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Pagination, Navigation, Autoplay } from "swiper/modules";
import BadgeReview from "./BadgeReviews";

export default function HeroSlider({ slides }) {
  const [isMobile, setIsMobile] = useState(false);

  if (typeof window === 'undefined') return null;
  return (
    <>
      {/* Hidden H1 for SEO */}
      <h1 className="visually-hidden">Flooring Installation Services</h1>

      <Swiper
        slidesPerView={1}
        loop={true}
        pagination={{ clickable: true }}
        navigation={true}
        autoplay={{
          delay: 6000,
          disableOnInteraction: false,
        }}
        modules={[Navigation, Pagination,Autoplay]}
        className="mySwiper"
      >
        <BadgeReview />

        {/* Loop through the slides data */}
        {slides.map((slide, index) => (
          <SwiperSlide key={index}>
            <div className="slider-container">
              <img src={slide.image} alt={slide.alt} className="slider-image" />

              <div className="slider-content slider-content-three">
                <h2 className="slide-title">{slide.title}</h2>
                <h3 className="slide-subtitle">{slide.subtitle}</h3>
                <div className="hero-button-wrapper">
                  {isMobile ? (
                    <a href="tel:+12085551234" className="hero-button">
                      Call Now
                    </a>
                  ) : (
                    <div className="hero-button" >
                      Live Chat
                    </div>
                  )}
                </div>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>

      <div className="swiper-pagination-custom custom-pagination"></div>
    </>
  );
}
